
.content-ul .content-li {
  box-sizing: border-box;
  padding: 0 0.3rem;
  background: #fff;
  margin-top: 0.25rem;
  box-shadow: 0 -2px 2px rgba(213, 215, 217, 0.8), 0 2px 2px rgba(213, 215, 217, 0.8);
}
.content-ul .content-li .header {
    height: 0.7rem;
    padding: 0.22rem 0;
}
.content-ul .content-li .header img {
      height: 100%;
      float: left;
      border-radius: 50%;
}
.content-ul .content-li .header span {
      float: left;
      height: 100%;
      margin-left: 0.2rem;
      line-height: 0.7rem;
}
.content-ul .content-li .header .name {
      color: #99A4AA;
}
.content-ul .content-li .header .top {
      color: red;
}
.content-ul .content-li .header .tab {
      float: right;
      color: #272727;
}
.content-ul .content-li .title {
    font-family: "Microsoft YaHei";
    font-size: 14px;
}
.content-ul .content-li .text-content {
    color: #474747;
    padding: 0.2rem 0;
}
.content-ul .content-li .related {
    color: #99A4AA;
    padding-bottom: 0.2rem;
}



